<template>
  <el-form status-icon ref="ruleForm" label-width="100px" class="demo-ruleForm">
    <el-form-item label="账号" prop="account">
      <el-select v-model="accountType" placeholder="请选择账号类型">
        <el-option label="学生" value="学生"></el-option>
        <el-option label="职工" value="职工"></el-option>
        <el-option label="管理员" value="管理员"></el-option>
      </el-select>
      <el-input v-model="account" placeholder="请输入账号"></el-input>
    </el-form-item>
    <el-form-item label="密码" prop="pass">
      <el-input type="password" autocomplete="off" v-model="pwd1"></el-input>
    </el-form-item>
    <el-form-item label="确认密码" prop="checkPass">
      <el-input type="password" autocomplete="off" v-model="pwd2"></el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="updata">提交</el-button>
      <el-button @click="clear">重置</el-button>
    </el-form-item>
  </el-form>
</template>

<script>
export default {
  data() {
    return {
      accountType: '', // 选中的账号类型
      account: '',     // 账号
      pwd1: '',        // 密码
      pwd2: ''         // 确认密码
    };
  },
  methods: {
    clear() {
      this.accountType = '';
      this.account = '';
      this.pwd1 = '';
      this.pwd2 = '';
    },
    updata(){
      if (this.pwd1 !== this.pwd2) {
        alert("两次输入的密码不一致！");
        return;
      }
      
      let params = new URLSearchParams();
      params.append('username', this.account);
      params.append('newcode', this.pwd2);
      params.append('type', this.accountType);
      
      this.$axios({
        method: "post",
        url: "http://127.0.0.1:8690/updatacodeadm",
        data: params,
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
        }
      }).then((result) => {
        console.log(result.data);
        if(result.data.message =="Password updated successfully"){
          this.$message({
          type: 'success',
          message: '修改成功!'
        });
          this.pwd1=''
          this.pwd2=''
          this.account=''
          this.accountType=null
        }else{
            this.$message({
          type: 'warning',
          message: '原始密码不正确'
        });
        }
      }).catch((error) => {
         this.$message({
          type: 'warning',
          message: '修改失败'
        });
        console.error('请求失败:', error);
        
      });
    }
  }
};
</script>
